
.titleStyledText { 
    font-family: 'Nasalization';
    src: url('../fonts/nasalization-rg.woff2') format('woff2');
    font-size: 30px;
    font-weight: bold;
    color:#6a11cb;
    cursor: pointer;
    transition: text-shadow 0.3s ease;
    mix-blend-mode: #6a11cb
}

.headerButtonStylesText {
    font-family: 'STIX Two Text', serif;
    font-size: 20px;
    font-weight: bold;
    color:#6a11cb;
    cursor: pointer;
    transition: text-shadow 0.3s ease;
    margin-top: 10px;
    margin-right: 10px;
}

.headerButtonStylesText:hover {
    text-shadow: 2px 2px 4px #000000;
}


.titleStyledText:hover {
    text-shadow: 2px 2px 4px #000000;
}

/*for header buttons on right side*/
.headerDetailedButtonStylesText {
    display: flex;
    justify-content: space-between;
    align-items: right;
    font-family: 'STIX Two Text', serif;
    font-size: 23px;
    font-weight: bold;
    color:#6a11cb;
    cursor: pointer;
    transition: text-shadow 0.3s ease;
    margin-top: 10px;
    margin-right: 10px;
}

.headerBasicButtonStylesText {
    display: flex;
    justify-content: space-between;
    align-items: right;
    font-family: 'STIX Two Text', serif;
    font-size: 23px;
    font-weight: bold;
    color:#6a11cb;
    cursor: pointer;
    transition: text-shadow 0.3s ease;
    margin-top: 10px;
    margin-right: 3px;
}

.headerRightButtons {
    display: flex;
    align-items: center;
}

.headerRightButtons:hover {
    text-shadow: 2px 2px 4px #000000;
}

/*for vertical line separating buttons*/
.verticalLine {
    height: 25px; /* Adjust height as needed */
    width: 1.5px;
    background-color: #6a11cb; /* Color of the vertical line */
    margin: 7px 7px 0; /* Adjust margin to center vertically */
}


.header {
    background-color: white;
    background-size: 100% 100%; 
    background-position: center;
    background-repeat: no-repeat;
}

.pageTop {
    padding: 40px;
    background-image: linear-gradient(to right, #6a11cb, #2575fc);
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}


.pageBody {
    padding: 40px;
    background-image: linear-gradient(to right, #6a11cb, #2575fc);
    color: white;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.customButton {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    height: auto;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 50px;
    border-radius: 10px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1);
    background-color: rgba(255, 255, 255, 0.8);
    color: #6a11cb;
    border: none;
    transition: all 0.3s ease;
    text-align: justify;
}

.customButton:hover {
    transform: translateY(-10px);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    color: #6a11cb;
    background-color:rgba(255, 255, 255, 0.8);
}

.column {
    margin-left: 2.5%;
    margin-right: 2.5%;
}

.footer {
    text-align: center;
    padding: 20px;
    background-color: white;
    color: #6a11cb;
    font-size: 14px;
}

@media (max-width: 768px) {
    .customButton {
        margin-top: 10px;
    }

    .column {
        margin: 0 10px;
    }
}

